import {ApiDataProps, DataItemProps} from '@er/types';
import {PngDataAwareProps} from '../../types/props';

export interface PngListBoxProps extends PngDataAwareProps {
  multiple?: boolean;
  listStyle?: any;
  readonly?: boolean;
  disabled?: boolean;
  checkbox?: boolean;
  filter?: boolean;
  filterMode?: string;
  metaKeySelection?: boolean;
  dataKey?: string;
  showToggleAll?: boolean;
  optionLabel?: string;
  onDblClick?: (ctx) => void;
  onChange?: (ctx) => void;
  $ext?: {
    showValue?: boolean;
    field?: string;
    dataItems?: {}[];
    apiDataProps?: ApiDataProps,
    dataItemProps?: DataItemProps,
    header?: (ctx) => string,
    footer?: (ctx) => string,
  };
}

export const DEFAULT_PNG_LIST_BOX_PROPS: PngListBoxProps = {
  optionLabel: 'label',
  checkbox: true,
  multiple: true,
  showToggleAll: true,
  $ext: {
    dataItemProps: {
      itemStyleClass: 'd-inline-flex ml-2'
    }
  }
};
