import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {BaseComponent} from '@er/core';
import {NotifyLevel, NotifyProp, NotifyType, StateNames} from '@er/types';
import {ConfigUtils, StatesUtils} from '@er/utils';
import {MessageService} from 'primeng/api';
import {Message} from 'primeng/components/common/message';
import {filter} from 'rxjs/operators';


export const DEFAULT_ANNOUNCE_PROPS: NotifyProp = {
  level: NotifyLevel.INFO,
  animation: true,
  closable: true,
  baseZIndex: 2000
};

@Component({
  selector: 'png-announce',
  template: `
    <p-messages [styleClass]="announce.styleClass"
                [style]="announce.style"
                [closable]="announce.closable"
    ></p-messages>
  `,
  providers: [MessageService],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PngAnnounceComponent extends BaseComponent implements OnInit {

  announce: NotifyProp;

  private defaultNotifyType: NotifyType;

  constructor(private messageService: MessageService, private cd: ChangeDetectorRef) {
    super();
    this.announce = {
      ...DEFAULT_ANNOUNCE_PROPS,
      ...ConfigUtils.getConfig().announce || {}
    };
    this.defaultNotifyType = ConfigUtils.getConfig().notify;
  }

  ngOnInit() {
    this.$subscriptions = StatesUtils.observe(StateNames.notify)
      .pipe(
        filter(notify => notify && notify.message),
        filter(notify => notify.type === NotifyType.ANNOUNCE || (!notify.type && this.defaultNotifyType === NotifyType.ANNOUNCE))
      ).subscribe(notify => {
        const model = this.getNotifyModel(notify);
        this.messageService.add(model);
        this.cd.detectChanges();
      });
  }

  getNotifyModel(notify: NotifyProp): Message {
    return {
      key: notify.key,
      severity: notify.level || NotifyLevel.INFO,
      summary: notify.title,
      detail: notify.message,
      closable: notify.closable,
      data: notify.data
    };
  }

}


