import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {BaseComponent} from '@er/core';
import {DynaContent, PopupDialogProp, StateNames} from '@er/types';
import {StatesUtils} from '@er/utils';
import {Observable, of} from 'rxjs';
import {filter, map, takeUntil} from 'rxjs/operators';
import {DEFAULT_POPUP_DIALOG_PROPS} from './defaults';


@Component({
  selector: 'png-popup-dialog',
  templateUrl: 'tpl.html',
  styleUrls: ['styles.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PngPopupDialogComponent extends BaseComponent implements OnInit {

  @Input() header: string;
  @Input() content: DynaContent;
  @Input() canClose: (context) => boolean;

  @Output() onClose = new EventEmitter<any>();
  @Output() onAction = new EventEmitter<any>();

  dlgOptions$: Observable<PopupDialogProp>;

  templateNames = {
    header: 'header',
    content: 'content',
    footer: 'footer'
  };

  @Input()
  set options(options) {
    this.dlgOptions$ = of(this.resolveDlgOptions(options));
  }

  ngOnInit(): void {
    this.dlgOptions$ = StatesUtils.observe(StateNames.popup)
      .pipe(
        filter(state => !!state),
        map(options => this.resolveDlgOptions(options)),
        takeUntil(this.destroyed$)
      );
  }

  resolveDlgOptions(options): PopupDialogProp {
    const opt = {
      ...DEFAULT_POPUP_DIALOG_PROPS,
      header: this.header,
      content: this.content,
      ...options
    };
    if (options.contentMaxHeight || options.contentMaxWidth) {
      opt.contentStyle = {
        ...opt.contentStyle || {},
        'max-height': `${options.contentMaxHeight || 3000}px`,
        'max-width': `${options.contentMaxWidth || 3000}px`
      };
    }
    return opt;
  }

  showEvent(dlg, options) {
    super.emitEvent('onShow', {
      scope: options,
      event: dlg,
      context: options.context
    });
  }

  closeDlg(dlg, options) {
    if (this.canClose && !this.canClose({
      scope: options,
      event: dlg,
      context: options.context
    })) {
      return;
    }
    StatesUtils.create(StateNames.popup, {visible: false});
  }

}
