import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from '@er/core';
import {DialogUtils} from '@er/utils';

@Component({
  selector: 'png-search-box',
  template: `
    <span class="inner-addon" [ngClass]="styleClass" [ngStyle]="style">
      <i class="fa fa-search text-primary left"></i>
      <input [title]="title" pInputText placeholder="" [(ngModel)]="searchKey" (keydown.enter)="search()" (keyup)="keyChange()"/>
      <i *ngIf="searchKey" class="fa fa-trash gray right" [title]="title" (click)="clearSearch()"></i>
   </span>
    <span class="position-container"><i title="" class="fa fa-question-circle green position-absolute" style="right:2px"
                                        (click)="showHelp()"></i></span>

  `
})

export class PngSearchBoxComponent extends BaseComponent {

  searchKey: string;

  searched = false;

  @Input() styleClass: string;

  @Input() style: any;

  @Input() title = ',';

  @Output() onSearch = new EventEmitter();

  @Output() onSearchChange = new EventEmitter();

  search() {
    this.searched = true;
    this.onSearch.emit(this.searchKey);
  }

  keyChange() {
    if (!this.searchKey || this.searchKey.length === 0) {
      this.clearSearch();
    }
    this.onSearchChange.emit(this.searchKey);
  }

  clearSearch() {
    this.searchKey = undefined;
    if (this.searched) {
      this.onSearch.emit(undefined);
    }
    this.searched = false;
  }

  showHelp() {
    DialogUtils.info('', `
    <div class="ml-3">
       <div><b class="font-weight-bold">+</b> </div>
       <div><b class="font-weight-bold">|</b> </div>
       <div><b class="font-weight-bold">-</b> </div>
       <div><b class="font-weight-bold">*</b> </div>
       <div><b class="font-weight-bold">( )</b> </div>
    </div>
    `);
  }
}
