import {ChangeDetectionStrategy, Component} from '@angular/core';
import {StateNames} from '@er/types';
import {StatesUtils} from '@er/utils';
import {PngAdminWorkSpaceComponent} from '../component';

@Component({
  selector: 'png-ws-topbar',
  templateUrl: 'tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WorkSpaceTopBarComponent {

  constructor(public ws: PngAdminWorkSpaceComponent) {
  }

  toggleMainMenu() {
    StatesUtils.toggle(StateNames.menu, this.ws.rootMenuToggleStateName);
    event.preventDefault();
  }
}
