import {ApiDataProps, DataItemProps} from '@er/types';
import {TreeNode} from 'primeng/api';
import {PngDataAwareProps} from '../../types/props';

export interface PngTreeNodeProps extends TreeNode {
  [prop: string]: any;
}

export interface PngTreeProps extends PngDataAwareProps {

  nodes?: PngTreeNodeProps[];

  selectionMode?: 'single' | 'multiple' | 'checkbox';

  selection?: TreeNode | TreeNode[];

  contextMenu?: any;

  layout?: 'vertical' | 'horizontal';

  draggableScope?: any;

  droppableScope?: any;

  draggableNodes?: boolean;

  droppableNodes?: boolean;

  metaKeySelection?: boolean;

  propagateSelectionUp?: boolean;

  propagateSelectionDown?: boolean;

  loadingIcon?: string;

  emptyMessage?: string;

  selectionChange?: (event) => void;

  onNodeSelect?: (event) => void;

  onNodeUnselect?: (event) => void;

  onNodeExpand?: (event) => void;

  onNodeCollapse?: (event) => void;

  onNodeContextMenuSelect?: (event) => void;

  onNodeDrop?: (event) => void;

  $container?: any;

  $ext?: {
    root?: string | TreeNode;
    virtualRoot?: string | TreeNode;
    hasSearchBox?: boolean;
    dataItems?: {}[];
    apiDataProps?: ApiDataProps
    dataItemProps?: DataItemProps
    caption?: string;
    captionIcon?: string;
    nodesFormat?: 'standard' | 'plainArray';
    icon?: string;
    collapsedIcon?: string | { method: ((node) => string) };
    expandedIcon?: string | { method: ((node) => string) };
    selectable?: boolean | { method: ((node) => boolean) };
    autoExpandLevel?: number;
    lazy?: boolean;
    filter?: string | { method: ((nodes: {}[]) => {}[]) };
    onLazyLoad?: (event) => { node?: TreeNode, data: any };
  };
}


export const PNG_TREE_NODE_KEYS = {
  idKey: 'id',
  labelKey: 'label',
  parentKey: 'parent',
  childrenKey: 'children',
  leafKey: 'leaf',
  dataKey: 'data',
  expandedKey: 'expanded'
};

export const DEFAULT_PNG_TREE_NODE_PROPS: PngTreeNodeProps = {};

export const DEFAULT_PNG_TREE_PROPS: PngTreeProps = {
  style: {'max-height.px': 500, 'max-width.px': 350, 'overflow': 'auto'},
  emptyMessage: '',
  $ext: {
    apiDataProps: {
      dataPath: 'items',
      size: 9999
    },
    dataItemProps: {
      idKey: 'id',
      parentKey: 'parent',
      labelKey: 'name',
      leafKey: 'leaf'
    },
    nodesFormat: 'plainArray',
    lazy: false,
    hasSearchBox: true,
    autoExpandLevel: 1,
    expandedIcon: 'fa fa-folder-open',
    collapsedIcon: 'fa fa-folder',
    icon: 'fa fa-leaf'
  }
};
