export const defaultRowIndexColumn = <PngTableColumnProps> {
  label: '',
  width: '50px',
  cellClass: 'text-right',
  sortable: false,
  filterable: false,
  aggable: false,
  order: -100,
  isDataColumn: false,
  cellContent: (data) => data.rowIndex + 1
};

export const defaultOrderedRowIndexColumn = {
  label: '',
  width: '50px',
  cellClass: 'text-right',
  sortable: false,
  filterable: false,
  aggable: false,
  order: -100,
  isDataColumn: false,
  cellContent: (data) => data.rowIndex + 1,
  cellStyleClass: (ctx) => {
    if (ctx.rowIndex === 0) {
      return 'badge badge-pill white bg-red No-1';
    } else if (ctx.rowIndex === 1) {
      return 'badge badge-pill black bg-yellow No-2';
    } else if (ctx.rowIndex === 2) {
      return 'badge badge-pill white bg-blue No-3';
    } else {
      return 'badge badge-pill badge-dark';
    }
  }
};

export const defaultActionColumn = <PngTableColumnProps> {
  label: '',
  width: '150px',
  cellStyleClass: 'text-left',
  sortable: false,
  filterable: false,
  aggable: false,
  dataAttr: DataAttr.BUTTONS,
  isActionColumn: true,
  cellContent: <ButtonProps[]> []
};

export const defaultAddButton = <ButtonProps> {
  $id: 'add',
  title: '',
  label: '',
  icon: 'fa fa-plus',
  styleClass: 'ui-button-rounded ui-button-success',
  onClick: (event) => {
    event.pngTable.navigate(event.pngTable.$props.$ext.addRouter, [], {tableId: event.pngTable.$id});
  }
};

export const defaultViewButton = <ButtonProps> {
  $id: 'view',
  title: '',
  icon: 'fa fa-edit',
  styleClass: 'ui-button-rounded ui-button-info',
  onClick: (event) => {
    event.pngTable.navigate(event.pngTable.$props.$ext.viewRouter, [event.row['id']]);
  }
};

export const defaultEditButton = <ButtonProps> {
  $id: 'edit',
  title: '',
  icon: 'fa fa-edit',
  styleClass: 'ui-button-rounded ui-button-warning',
  onClick: (event) => {
    event.pngTable.navigate(event.pngTable.$props.$ext.editRouter, [event.row['id']], {tableId: event.pngTable.$id});
  }
};

export const defaultDeleteButton = <ButtonProps> {
  $id: 'delete',
  title: '',
  icon: 'fa fa-trash',
  styleClass: 'ui-button-rounded ui-button-danger',
  onClick: (event) => {
    ApiUtils.deleteById(event.pngTable.$props.$ext.apiDataProps.apiEntry, event.row['id'])
      .subscribe(() => {
        event.pngTable.load();
        StatesUtils.update(event.pngTable.$props.$id, {deleted: event.row['id']});
      });
  }
};

export const defaultAuditButton = <ButtonProps> {
  $id: 'audit',
  title: '',
  icon: 'fa fa-check',
  styleClass: 'ui-button-rounded ui-button-success',
  onClick: (event) => {
    event.pngTable.navigate(event.pngTable.$props.$ext.auditRouter, [event.row['id']], {action: 'audit', tableId: event.pngTable.$id});
  }
};

export const defaultDownloadButton = <ButtonProps> {
  $id: 'download',
  title: '',
  icon: 'fa fa-download',
  overlay: true,
  styleClass: 'ui-button-rounded ui-button-success',
  onClick: (event) => {
    event.pngTable.navigate(event.pngTable.$props.$ext.downloadRouter, [event.row['id']]);
  }
};

export const defaultOverlayColumnProps = {
  sortable: false,
  filterable: false,
  aggable: false
};

export const defaultOverlayTableProps = {
  alwaysShowPaginator: false,
  scrollable: true,
  scrollWidth: '800px',
  paginatorPosition: 'bottom'
};
