import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {BaseComponent} from '@er/core';
import {NotifyLevel, NotifyProp, NotifyType, StateNames, UiPosition} from '@er/types';
import {ConfigUtils, StatesUtils} from '@er/utils';
import {MessageService} from 'primeng/api';
import {Message} from 'primeng/components/common/message';
import {filter} from 'rxjs/operators';


export const DEFAULT_ALERT_PROPS: NotifyProp = {
  animation: true,
  duration: 3000,
  immutable: true,
  closable: true,
  autoZIndex: true,
  position: UiPosition.rightTop
};

@Component({
  selector: 'png-alert',
  template: `
    <p-toast [styleClass]="alert.styleClass"
             [style]="alert.style"
             [position]="alert.position"
             [autoZIndex]="alert.autoZIndex"
    ></p-toast>
  `,
  providers: [MessageService],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PngAlertComponent extends BaseComponent implements OnInit {

  alert: NotifyProp;

  private defaultNotifyType: NotifyType;

  constructor(private messageService: MessageService, private cd: ChangeDetectorRef) {
    super();
    this.alert = {
      ...DEFAULT_ALERT_PROPS,
      ...ConfigUtils.getConfig().alert || {}
    };
    this.defaultNotifyType = ConfigUtils.getConfig().notify || NotifyType.ALERT;
  }

  ngOnInit() {
    this.$subscriptions = StatesUtils.observe(StateNames.notify)
      .pipe(
        filter(notify => notify && notify.message),
        filter(notify => notify.type === NotifyType.ALERT || (!notify.type && this.defaultNotifyType === NotifyType.ALERT))
      ).subscribe(notify => {
        const model = this.getNotifyModel(notify);
        this.messageService.add(model);
        this.cd.detectChanges();
      });
  }

  getNotifyModel(notify: NotifyProp): Message {
    return {
      key: notify.key,
      severity: notify.level || NotifyLevel.INFO,
      summary: notify.title,
      detail: notify.message,
      sticky: notify.sticky || false,
      life: notify.duration || notify.level === NotifyLevel.ERROR ? DEFAULT_ALERT_PROPS.duration * 2 : DEFAULT_ALERT_PROPS.duration,
      closable: notify.closable || true,
      data: notify.data
    };
  }

}


