import {ChangeDetectionStrategy, Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from '@er/core';
import {PngTableColumnProps} from '../../types';

@Component({
  selector: 'png-table-column-selector',
  template: `

    <p-multiSelect [style]="{height: '30px', minWidth: '150px'}"
                   [ngClass]="styleClass"
                   [ngStyle]="style"
                   [options]="optionColumns"
                   [(ngModel)]="columns"
                   optionLabel="label"
                   [selectedItemsLabel]="'{0}('+optionColumns?.length+')'"
                   defaultLabel=""
                   (onChange)="changeColumns($event)"
    >
    </p-multiSelect>

  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class PngTableColumnSelectorComponent extends BaseComponent {

  @Input() columns: PngTableColumnProps[];

  @Input() optionColumns: PngTableColumnProps[];

  @Input() styleClass: any;

  @Input() style: {};

  @Output() onColumnsChange = new EventEmitter();

  changeColumns(event) {
    event.value.forEach(column => column.hidden = false);
    this.onColumnsChange.emit(event);
  }

}
