import {Component, Input} from '@angular/core';
import {BaseComponent} from '@er/core';
import {PngTableColumnProps} from '../../types';
import {PngTableComponent} from '../table/component';

@Component({
  selector: 'tr[png-table-footer]',
  template: `
    <ng-container *ngIf="customerTpl;else default">
      <ng-container *ngTemplateOutlet="customerTpl; context:{columns:columns,context:pngTable}"></ng-container>
    </ng-container>
    <ng-template #default></ng-template>
  `
})

export class PngTableFooterComponent extends BaseComponent {

  @Input() columns: PngTableColumnProps[];

  constructor(public pngTable: PngTableComponent) {
    super();
  }
}
