import {ChangeDetectionStrategy, Component, Input, OnInit, QueryList, ViewChildren} from '@angular/core';
import {BaseComponent, DataFieldUtils, SelectorDirective} from '@er/core';
import {DataAttr, DataType, Overlay, OverlayContext, OverlayType} from '@er/types';
import {CommonsUtils} from '@er/utils';
import {PngTableColumnProps} from '../../types';
import {PngTableBodyCellComponent} from '../body-cell/component';
import {PngTableComponent} from '../table/component';

@Component({
  selector: 'png-table-body-cell-content',
  templateUrl: 'tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class PngTableBodyCellContentComponent extends BaseComponent implements OnInit {

  DATA_ATTR = DataAttr;

  DATA_TYPE = DataType;

  OVERLAY_TYPE = OverlayType;

  @Input() column: PngTableColumnProps;

  @Input() row: {}[];

  @Input() rowIndex: number;

  @ViewChildren(SelectorDirective) selectors: QueryList<SelectorDirective>;

  constructor(public pngTable: PngTableComponent,
              private cell: PngTableBodyCellComponent) {
    super();
  }

  get cellContent() {
    if (this.column.dataAttr === DataAttr.BUTTONS) {
      let btns = this.column.cellContent;
      btns = CommonsUtils.getArrayValue(btns);
      btns = btns.filter(btn => !btn.show || btn.show === true || btn.show.method === true || (CommonsUtils.isFunction(btn.show.method) && <Function>(btn.show.method).call(this, this.cellContext())));
      return btns;
    }
    let content = this.getValue(this.column.cellContent || this.row[this.column.key]);
    if (CommonsUtils.isEmpty(content) && this.column.defaultCellContent !== undefined) {
      content = this.column.defaultCellContent;
    }
    if (this.column.contentKey) {
      content = CommonsUtils.get(content, this.column.contentKey);
      if (CommonsUtils.isArray(content)) {
        content = <string[]>content.join(',');
      }
    } else if (this.column.format) {
      CommonsUtils.format(content, this.column.format);
    } else if (CommonsUtils.isFunction(this.column.formatter)) {
      content = (this.column.formatter as Function)(content);
    } else if (this.column.formatter || CommonsUtils.isTemplateStr(content)) {
      content = CommonsUtils.templateStr((this.column.formatter as string) || content, this.row);
    } else if (CommonsUtils.isJson(content)) {
      content = DataFieldUtils.getFieldDisplayContent(content, this.column.dataField);
    }
    if (CommonsUtils.isString(content) && this.column.maxLength && content.length > this.column.maxLength) {
      content = content.substr(0, this.column.maxLength) + '...';
    }
    if (content === true) {
      return '';
    } else if (content === false) {
      return '';
    }
    return content;
  }

  get cellIcon() {
    return this.getValue(this.column.cellIcon);
  }

  get toolTip() {
    const toolTip = this.getValue(this.column.toolTip);
    if (!toolTip && this.column.maxLength && this.row[this.column.key] && this.row[this.column.key].length > this.column.maxLength) {
      return this.row[this.column.key];
    }
    return toolTip;
  }

  get cellClass() {
    return CommonsUtils.getValue(this.column.cellStyleClass, this.cellContext());
  }

  get cellStyle() {
    return CommonsUtils.getValue(this.column.cellStyle, this.cellContext());
  }

  get routLink() {
    if (!this.column.routLink) {
      return;
    }
    let routerLink = this.column.routLink;
    if (CommonsUtils.isString(routerLink)) {
      routerLink = [routerLink];
    }
    let routeParams: any[];
    if (this.column.routParam) {
      if (CommonsUtils.isFunction(this.column.routParam)) {
        routeParams = (<Function>this.column.routParam).call(this.pngTable, this.column, this.row) || [];
      } else if (CommonsUtils.isArray(this.column.routParam)) {
        routeParams = (<any[]>this.column.routParam).map(param => this.row[param]) || [];
      } else if (CommonsUtils.isString(this.column.routParam)) {
        routeParams = [this.row[<string>this.column.routParam]] || [];
      } else if (this.pngTable.$props.dataKey) {
        routeParams = [this.row[this.pngTable.$props.dataKey]] || [];
      }
    }
    if (routeParams) {
      routerLink = [...<any[]>routerLink, ...routeParams];
    }
    return routerLink;
  }

  get queryParam() {
    if (!this.column.routQueryParams) {
      return;
    }
    if (CommonsUtils.isFunction(this.column.routQueryParams)) {
      return (<Function>this.column.routQueryParams).call(this.pngTable, this.column, this.row);
    } else if (CommonsUtils.isArray(this.column.routQueryParams)) {
      const params = {};
      (<any[]>this.column.routQueryParams).map(param => params[param] = this.row[param]);
      return params;
    } else if (CommonsUtils.isString(this.column.routQueryParams)) {
      return {[<string>this.column.routQueryParams]: this.row[<string>this.column.routQueryParams]};
    }
    return undefined;
  }

  iconClass(icon) {
    if (icon && icon.trim().startsWith('fa-')) {
      return `fa ${icon}`;
    }
    return icon;
  }

  hasAttr(attr) {
    return this.column.dataAttr === attr ||
      (CommonsUtils.isArray(this.column.dataAttr) && (this.column.dataAttr as DataAttr[]).indexOf(attr) >= 0);
  }

  overlayContext(overlay, index, event?): OverlayContext {
    return {
      ...this.cellContext(event),
      type: overlay.type,
      content: this.getValue(overlay.content),
      context: overlay.context,
      styleClass: this.getValue(overlay.styleClass),
      style: this.getValue(overlay.style),
      overlayStyleClass: this.getValue(overlay.overlayStyleClass),
      overlayStyle: this.getValue(overlay.overlayStyle),
      id: `overlay-cell-${this.column.key}-${index}`
    };
  }

  treeContent(content) {
    if (CommonsUtils.isArray(content)) {
      return '' + (content as any[]).length + '';
    } else if (CommonsUtils.isJson(content)) {
      return '' + Object.keys(content).length + '';
    } else {
      return content;
    }
  }

  overlayTreeContext(content, event?): OverlayContext {
    return {
      ...this.cellContext(event),
      type: OverlayType.TREE,
      content: content
    };
  }

  cellContext(event?) {
    return this.cell.cellContext(event);
  }

  ngOnInit(): void {
    if (this.column.overlays) {
      (<Overlay[]>this.column.overlays).filter(overlay => overlay.type === OverlayType.MENU)
        .forEach(overlay => {
          if (overlay.content['menuItem'].command) {
            const command = overlay.content['menuItem'].command;
            overlay.content['menuItem'].command = (event) => command.call(this, this.cellContext(event));
          }
        });
    }
  }

  cellClick(event) {
    event.preventDefault();
    if (this.column.onClick) {
      this.column.onClick.call(this.pngTable, this.cellContext(event));
    }
  }

  toggleTip(event, tipText) {
    if (tipText) {
      this.pngTable.showOverlay(<OverlayContext>{
        $event: event,
        content: tipText,
        styleClass: 'alert alert-info',
        style: {
          overflow: 'auto',
          width: '300px',
          height: '300px'
        }
      });
    }
  }

  private getValue(target) {
    return CommonsUtils.getValue(target, this.cellContext()) || '';
  }
}
