import {Component, forwardRef} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {DataAwareControlWrapperComponent} from '@er/core';
import {CommonsUtils} from '@er/utils';
import {DEFAULT_PNG_SLIDER_PROPS, PngSliderProps} from './type';

export {PngSliderProps} from './type';

@Component({
  selector: 'png-slider',
  template: `
    <small class="d-inline w-25">{{current}}</small>
    <p-slider class="d-inline"
              [erApiData]="apiDataProps"
              [(ngModel)]="value"
              erPropsBind
              [props]="$props"
              [min]="min"
              [max]="max"
              (onChange)="updateTouch()"
              (onDataReady)="initValue($event)"
    >
    </p-slider>

  `,
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => PngSliderComponent),
    multi: true
  }]
})

export class PngSliderComponent extends DataAwareControlWrapperComponent<PngSliderProps> {

  $defaultProps = DEFAULT_PNG_SLIDER_PROPS;

  min: number;

  max: number;

  get current() {
    return CommonsUtils.join(this.value, '');
  }

  initValue(event) {
    if (event.hasOwnProperty('min') && event.hasOwnProperty('max')) {
      this.min = event.min;
      this.max = event.max;
      this.value = [event.min, event.max];
    } else {
      this.value = event;
    }
  }
}
