import {ChangeDetectionStrategy, Component, Input} from '@angular/core';
import {BaseComponent} from '@er/core';
import {ButtonProps} from '@er/types';

@Component({
  selector: 'png-buttons',
  templateUrl: 'tpl.html',
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class PngButtonsComponent extends BaseComponent {

  @Input() context: any;

  @Input() btns: ButtonProps[];

  @Input() style: {};

  @Input() styleClass: string;

  get hasOverlay() {
    return this.btns && this.btns.some(btn => !!btn.overlay);
  }

  toggle(event, panel) {
    panel.toggle(event);
  }

}
