import {ChangeDetectionStrategy, Component} from '@angular/core';
import {StateNames} from '@er/types';
import {StatesUtils} from '@er/utils';
import {distinctUntilChanged, map} from 'rxjs/operators';
import {PngAdminWorkSpaceComponent} from '../component';

@Component({
  selector: 'png-ws-footer',
  templateUrl: './tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WorkSpaceFooterComponent {

  constructor(public ws: PngAdminWorkSpaceComponent) {
  }

  get elapseTime$() {
    return StatesUtils.observe(StateNames.httpTimer)
      .pipe(
        distinctUntilChanged(),
        map(v => v && v['elapsed'])
      );
  }
}
