import {Component, forwardRef} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {DataAwareControlWrapperComponent} from '@er/core';
import {DEFAULT_SELECT_BUTTON_PROPS, PngSelectButtonProps} from './type';

export {PngSelectButtonProps} from './type';

@Component({
  selector: 'png-select-button',
  template: `
    <p-selectButton
      [(ngModel)]="value"
      erPropsBind
      [props]="$props"
      [context]="ctx"
      [erApiData]="apiDataProps"
      [options]="data$|async|erDataItems:$props.$ext.dataItemProps"
      (onOptionClick)="updateTouch()"
    >
      <ng-template let-item>
        <er-data-item [customerTpl]="template(templateNames.item)"
                      [item]="item.data"
                      [props]="$props.$ext.dataItemProps">
        </er-data-item>
      </ng-template>  
    </p-selectButton>
  `,
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => PngSelectButtonComponent),
    multi: true
  }]
})

export class PngSelectButtonComponent extends DataAwareControlWrapperComponent<PngSelectButtonProps> {

  $defaultProps = DEFAULT_SELECT_BUTTON_PROPS;

  templateNames = {
    item: 'item'
  };

}
