import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewChild
} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {BaseComponent, HttpUtils} from '@er/core';
import {HttpResponseType, PopupDialogProp} from '@er/types';
import {CommonsUtils, ConfigUtils, DialogUtils} from '@er/utils';
import {Lightbox} from 'primeng/primeng';


@Component({
  selector: 'png-image',
  template: `
    <div [ngClass]="styleClass" [ngStyle]="style">
      <img *ngIf="image"
           style="margin: 5px"
           [src]="image"
           [ngClass]="imageStyleClass"
           [ngStyle]="imageStyle"
           [title]="description || ''"
           (click)="viewImage()"/>
      <div *ngIf="showDescription" [ngClass]="descriptionStyleClass" [ngStyle]="descriptionStyle">
        {{description}}
      </div>
    </div>
    <ng-template #imageView>
      <img [src]="fullImage" [ngStyle]="imageViewStyle" [ngClass]="imageViewStyleClass"/>
    </ng-template>
    <ng-template #imagesView>
      <p-lightbox [images]="images" [styleClass]="boxStyleClass" [style]="boxStyle"></p-lightbox>
    </ng-template>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class PngImageComponent extends BaseComponent implements OnChanges {
  @Input() src: any;
  @Input() styleClass: any;
  @Input() style: any;
  @Input() imageStyleClass: any;
  @Input() imageStyle: any;
  @Input() descriptionStyleClass: any;
  @Input() descriptionStyle: any;
  @Input() boxStyleClass: any;
  @Input() boxStyle: any;
  @Input() imageViewStyleClass: any;
  @Input() imageViewStyle: any = {'max-height': '300px', 'max-width': '500px', 'overflow': 'auto'};
  @Input() showDescription = true;

  @Input() size: 'small' | 'medium';
  @Input() fileId: string;

  @ViewChild('imageView') imageViewTpl: TemplateRef<any>;

  @ViewChild('imagesView') imageViewer: Lightbox;

  image: any;

  fullImage: any;

  images: {}[];
  @Output() descriptionChange = new EventEmitter();

  constructor(private cd: ChangeDetectorRef, public sanitizer: DomSanitizer) {
    super();
  }

  _description: string;

  get description() {
    return this._description;
  }

  set description(description) {
    this._description = description;
    this.descriptionChange.emit(description);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.src) {
      if (CommonsUtils.isArray(this.src)) {
        if (this.src.length === 1) {
          this.images = undefined;
          this.image = this.src[0];
          this.fullImage = this.src[0];
        } else {
          this.images = [];
          this.image = this.src[0];
          this.fullImage = this.src[0];
          (<any[]>this.src).forEach(image => {
            if (CommonsUtils.isString(image)) {
              this.images.push({source: image});
            } else {
              this.images.push(image);
            }
          });
        }
      } else {
        this.images = undefined;
        this.image = this.src;
        this.fullImage = this.src;
      }
    }
    if (changes.fileId) {
      let fileId;
      if (!this.fileId) {
        this.images = undefined;
        this.image = null;
      } else {
        if (CommonsUtils.isArray(this.fileId)) {
          if (this.fileId.length === 0) {
            this.images = undefined;
            this.image = null;
            return;
          } else {
            fileId = this.fileId[0];
          }
        } else {
          fileId = this.fileId;
        }
        HttpUtils.request(
          {
            uri: `${ConfigUtils.getConfig().api.servers['image'].uri}/${fileId}${this.size ? '?' + this.size : ''}`,
            observe: 'events',
            responseType: HttpResponseType.BLOB
          }).subscribe(response => {
          const objectURL = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(response.body)));
          this.images = undefined;
          this.image = objectURL;
          this.fullImage = `${ConfigUtils.getConfig().api.servers['image'].uri}/${fileId}`;
          const descriptions = response.headers.getAll('description');
          if (descriptions && descriptions.length > 0) {
            this.description = descriptions[0];
          }
          this.cd.detectChanges();
        });
      }
    }
  }

  viewImage() {
    if (this.images) {
      this.imageViewer.show();
      this.imageViewer.center();
    } else if (this.image) {
      DialogUtils.info('', this.imageViewTpl, <PopupDialogProp>{});
    }
  }
}
