import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  QueryList,
  SimpleChanges
} from '@angular/core';
import {BaseComponent} from '@er/core';
import {MenuItem} from 'primeng/components/common/api';
import {PngStepComponent} from './step.component';

export {PngWizardProps} from './type';

@Component({
  selector: 'png-wizard',
  template: `
    <ng-container *ngIf="template(templateNames.header)">
      <ng-container *ngTemplateOutlet="template(templateNames.header)"></ng-container>
    </ng-container>
    <p-steps [model]="items" [(activeIndex)]="activeIndex"
             [ngClass]="styleClass" [ngStyle]="style" [readonly]="false"></p-steps>
    <ng-content></ng-content>
    <button pButton type="button" *ngIf="activeIndex > 0" class="mr-2"
            (click)="previous()" icon="fa fa-hand-o-left" label="Previous"></button>
    <button pButton type="button" *ngIf="activeIndex < items.length - 1"
            (click)="next()" icon="fa fa-hand-o-right" iconPos="right" label="Next"></button>
    <ng-container *ngIf="template(templateNames.footer)">
      <ng-container *ngTemplateOutlet="template(templateNames.footer)"></ng-container>
    </ng-container>
  `,
  styleUrls: ['styles.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PngWizardComponent extends BaseComponent implements AfterContentInit, OnChanges {

  @Input() activeIndex = 0;

  @Input() styleClass: any;

  @Input() style: any;

  @Input() stepStyleClass: string;

  @Input() stepStyle: string;

  @Output() stepChange: EventEmitter<any> = new EventEmitter();

  items: MenuItem[] = [];

  @ContentChildren(PngStepComponent) steps: QueryList<PngStepComponent>;

  templateNames = {
    header: 'header',
    footer: 'footer'
  };

  ngAfterContentInit() {

    super.ngAfterContentInit();

    this.steps.toArray().forEach((step: PngStepComponent, index: number) => {
      if (!step.styleClass) {
        step.styleClass = this.stepStyleClass;
      }

      if (!step.style) {
        step.style = this.style;
      }

      if (index === this.activeIndex) {
        step.active = true;
      }

      this.items[index] = {
        label: step.label,
        command: (event: any) => {
          this.steps.toArray().forEach((s: PngStepComponent) => s.active = false);
          step.active = true;
          this.activeIndex = index;
          this.stepChange.emit(index);
        }
      };
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    if (!this.steps) {
      return;
    }

    for (const prop in changes) {
      if (prop === 'activeIndex') {
        const curIndex = changes[prop].currentValue;
        this.steps.toArray().forEach((step: PngStepComponent, index: number) => {
          const selected = index === curIndex;
          step.active = selected;
          if (selected) {
            // emit currently selected label
            // this.change.next(step.label);
          }
        });
        this.stepChange.emit(curIndex);
      }
    }
  }

  previous() {
    this.activeIndex--;
    this.stepChange.emit(this.activeIndex);
    this.ngOnChanges({
      activeIndex: {
        currentValue: this.activeIndex,
        previousValue: this.activeIndex + 1,
        firstChange: false,
        isFirstChange: () => false
      }
    });
  }

  next() {
    this.activeIndex++;
    this.stepChange.emit(this.activeIndex);
    this.ngOnChanges({
      activeIndex: {
        currentValue: this.activeIndex,
        previousValue: this.activeIndex - 1,
        firstChange: false,
        isFirstChange: () => false
      }
    });
  }

}
