import {Component, forwardRef} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {DataAwareControlWrapperComponent} from '@er/core';
import {DEFAULT_PNG_RATING_PROPS, PngRatingProps} from './type';

export {PngRatingProps} from  './type';

@Component({
  selector: 'png-rating',
  template: `
    <p-rating
      [(ngModel)]="value"
      erPropsBind
      [props]="$props"
      [context]="ctx"
      (onRate)="updateTouch()"
    >
    </p-rating>
  `,
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => PngRatingComponent),
    multi: true
  }]
})
export class PngRatingComponent extends DataAwareControlWrapperComponent<PngRatingProps> {

  $defaultProps = DEFAULT_PNG_RATING_PROPS;
}
