import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  Renderer2,
  ViewChild
} from '@angular/core';
import {BaseComponent} from '@er/core';
import {StateNames} from '@er/types';
import {ConfigUtils, StatesUtils} from '@er/utils';
import {ScrollPanel} from 'primeng/primeng';
import {filter} from 'rxjs/operators';

enum MenuOrientation {
  STATIC,
  OVERLAY,
  SLIM,
  HORIZONTAL
}

@Component({
  selector: 'png-admin-work-space',
  templateUrl: 'tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PngAdminWorkSpaceComponent extends BaseComponent implements OnInit, AfterViewInit {

  @Input() menuOrientation: MenuOrientation = MenuOrientation.STATIC;

  @Input() darkMenu = true;

  @Input() appName: string;

  @Input() appTitle: string;

  @Input() appLogo: string;

  @Input() httpMenuTag: string;

  @Output() onInitMenu = new EventEmitter();

  profileMode: 'top' | 'inline' = 'inline';

  rotateMenuButton: boolean;

  topbarMenuActive: boolean;

  overlayMenuActive: boolean;

  staticMenuDesktopInactive: boolean;

  staticMenuMobileActive: boolean;

  layoutMenuScroller: HTMLDivElement;

  menuClick: boolean;

  topbarItemClick: boolean;

  activeTopbarItem: any;

  resetMenu: boolean;

  menuHoverActive: boolean;

  rootMenuToggleStateName = '$rootMenuToggler';

  @ViewChild('layoutMenuScroller') layoutMenuScrollerViewChild: ScrollPanel;

  templateNames = {
    topbar: 'topbar',
    header: 'header',
    main: 'main',
    footer: 'footer',
    profile: 'profile',
    settings: 'settings',
    messages: 'messages',
    notifications: 'notifications',
    search: 'search'
  };

  constructor(public renderer: Renderer2, private cd: ChangeDetectorRef) {
    super();
  }

  ngOnInit(): void {
    this.onInitMenu.emit();
    this.$subscriptions = StatesUtils.observe(StateNames.menu, this.rootMenuToggleStateName)
      .pipe(
        filter(state => state !== undefined)
      )
      .subscribe(() => this._toggleMainMenu());
    this.appTitle = this.appTitle || ConfigUtils.getConfig().appTitle;
    this.appName = this.appName || ConfigUtils.getConfig().appName;
    this.appLogo = this.appLogo || ConfigUtils.getConfig().appLogo;
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.layoutMenuScrollerViewChild.moveBar();
    }, 100);
  }

  onLayoutClick() {
    if (!this.topbarItemClick) {
      this.activeTopbarItem = null;
      this.topbarMenuActive = false;
    }

    if (!this.menuClick) {
      if (this.isHorizontal() || this.isSlim()) {
        this.resetMenu = true;
      }

      if (this.overlayMenuActive || this.staticMenuMobileActive) {
        this.hideOverlayMenu();
      }

      this.menuHoverActive = false;
    }

    this.topbarItemClick = false;
    this.menuClick = false;
  }

  onMenuClick($event) {
    this.menuClick = true;
    this.resetMenu = false;

    if (!this.isHorizontal()) {
      setTimeout(() => {
        this.layoutMenuScrollerViewChild.moveBar();
      }, 450);
    }
  }

  onTopbarMenuButtonClick(event) {
    this.topbarItemClick = true;
    this.topbarMenuActive = !this.topbarMenuActive;

    this.hideOverlayMenu();

    event.preventDefault();
  }

  onTopbarItemClick(event, item) {
    this.topbarItemClick = true;

    if (this.activeTopbarItem === item) {
      this.activeTopbarItem = null;
    } else {
      this.activeTopbarItem = item;
    }

    event.preventDefault();
  }

  onTopbarSubItemClick(event) {
    event.preventDefault();
  }

  hideOverlayMenu() {
    this.rotateMenuButton = false;
    this.overlayMenuActive = false;
    this.staticMenuMobileActive = false;
  }

  isTablet() {
    const width = window.innerWidth;
    return width <= 1024 && width > 640;
  }

  isDesktop() {
    return window.innerWidth > 1024;
  }

  isMobile() {
    return window.innerWidth <= 640;
  }

  isOverlay() {
    return this.menuOrientation === MenuOrientation.OVERLAY;
  }

  isHorizontal() {
    return this.menuOrientation === MenuOrientation.HORIZONTAL;
  }

  isSlim() {
    return this.menuOrientation === MenuOrientation.SLIM;
  }

  changeToStaticMenu() {
    this.menuOrientation = MenuOrientation.STATIC;
  }

  changeToOverlayMenu() {
    this.menuOrientation = MenuOrientation.OVERLAY;
  }

  changeToHorizontalMenu() {
    this.menuOrientation = MenuOrientation.HORIZONTAL;
  }

  changeToSlimMenu() {
    this.menuOrientation = MenuOrientation.SLIM;
  }

  private _toggleMainMenu() {
    const menu = StatesUtils.getValue(StateNames.rootMenu);
    this.menuOrientation = menu && menu.orientation;
    this.darkMenu = menu && menu.dark;
    this.menuClick = true;
    this.rotateMenuButton = !this.rotateMenuButton;
    this.topbarMenuActive = false;

    if (this.menuOrientation === MenuOrientation.OVERLAY) {
      this.overlayMenuActive = !this.overlayMenuActive;
    } else {
      if (this.isDesktop()) {
        this.staticMenuDesktopInactive = !this.staticMenuDesktopInactive;
      } else {
        this.staticMenuMobileActive = !this.staticMenuMobileActive;
      }
    }
    this.cd.detectChanges();
  }
}
