import {Component, forwardRef} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {DataAwareControlWrapperComponent} from '@er/core';
import {DEFAULT_PNG_MULTI_SELECT_PROPS, PngMultiSelectProps} from './type';

export {PngMultiSelectProps} from './type';

@Component({
  selector: 'png-multi-select',
  template: `
    <p-multiSelect
      [(ngModel)]="value"
      erPropsBind
      [props]="$props"
      [context]="ctx"
      [erApiData]="apiDataProps"
      [options]="data$|async|erDataItems:$props.$ext.dataItemProps"
      (onBlur)="updateTouch()"
    >
      <ng-template let-item pTemplate="item">
        <er-data-item [customerTpl]="template('_')"
                         [item]="item.data"
                         [props]="$props.$ext.dataItemProps">
        </er-data-item>
      </ng-template>
    </p-multiSelect>
  `,
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => PngMultiSelectComponent),
    multi: true
  }]
})

export class PngMultiSelectComponent extends DataAwareControlWrapperComponent<PngMultiSelectProps> {

  $defaultProps = DEFAULT_PNG_MULTI_SELECT_PROPS;

}
