import {AfterViewInit, ChangeDetectionStrategy, Component, ElementRef, Input, Renderer2, ViewChild} from '@angular/core';
import {BaseComponent, DiUtils} from '@er/core';
import {DynaContent} from '@er/types';
import {DomUtils} from '@er/utils';

@Component({
  selector: 'png-panel',
  templateUrl: 'tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PngPanelComponent extends BaseComponent implements AfterViewInit {
  @Input() barStyle: {};
  @Input() barStyleClass: string;
  @Input() style: {};
  @Input() styleClass: string;
  @Input() header: DynaContent;
  @Input() footer: DynaContent;
  @Input() icon: string;
  @Input() collapsed = false;
  @Input() toggleable = false;
  @Input() showHeader = true;

  @ViewChild('pngPanel', {read: ElementRef}) panelEle: ElementRef;

  templateNames = {
    header: 'header',
    footer: 'footer'
  };

  ngAfterViewInit() {
    super.ngAfterViewInit();
    const titleBar: HTMLDivElement = this.panelEle.nativeElement.querySelector('.ui-panel-titlebar');
    if (titleBar) {
      if (this.barStyleClass) {
        DomUtils.addClasses(titleBar, this.barStyleClass);
      }
      if (this.barStyle) {
        Object.keys(this.barStyle).forEach(name => {
          DiUtils.get<Renderer2>(Renderer2).setStyle(titleBar, name, this.barStyle[name]);
        });
      }
    }
  }
}
