import {ChangeDetectionStrategy, Component, Input, ViewChild} from '@angular/core';
import {BaseComponent} from '@er/core';
import {Context, DynaContent} from '@er/types';
import {OverlayPanel} from 'primeng/primeng';

@Component({
  selector: 'png-overlay-panel',
  templateUrl: 'tpl.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class PngOverlayPanelComponent extends BaseComponent {

  @Input() content: DynaContent;

  @Input() context: Context;

  @ViewChild(OverlayPanel) panel: OverlayPanel;

  show(event) {
    this.panel.show(event);
  }

  toggle(event) {
    this.panel.toggle(event);
  }

  hide() {
    this.panel.hide();
  }
}
