import {Component, EventEmitter, Input, OnChanges, OnInit, Output} from '@angular/core';
import {BaseComponent} from '@er/core';
import {AggMethods, DataAttr, DataType} from '@er/types';
import {CommonsUtils, DataItemUtils} from '@er/utils';
import {BehaviorSubject} from 'rxjs';
import {PngAutoCompleteProps} from '../../../../auto-complete/type';
import {PngSliderProps} from '../../../../slider/component';
import {PngTableComponent} from '../../table/component';


@Component({
  selector: 'png-query-input',
  template: `
    <ng-container [ngSwitch]="queryDataType">
      <ng-container *ngSwitchCase="DATA_TYPE.NUMBER">
        <png-slider [(ngModel)]="queryValue"
                    erPropsBind
                    [props]="sliderProps"
        >
        </png-slider>
      </ng-container>
      <ng-container *ngSwitchCase="DATA_TYPE.BOOLEAN">
        <p-inputSwitch [(ngModel)]="queryValue">
        </p-inputSwitch>
      </ng-container>
      <ng-container *ngSwitchCase="DATA_TYPE.DATE">
        <ng-container *ngIf="!isTime">
          <png-calendar erPropsBind [(ngModel)]="queryValue"
                        [props]="{ selectionMode: 'range',placeholder:''}">
          </png-calendar>
        </ng-container>
        <ng-container *ngIf="isTime">
          <png-calendar erPropsBind [(ngModel)]="queryValue"
                        [props]="{ timeOnly: true, selectionMode: 'range',placeholder:''}"
          >
          </png-calendar>
        </ng-container>
      </ng-container>
      <ng-container *ngSwitchDefault>
        <png-auto-complete
          [(ngModel)]="queryValue"
          erPropsBind
          [props]="autoProps"
        >
        </png-auto-complete>
        <!--<input type="text" pInputText [(ngModel)]="queryValue" placeholder="">-->
      </ng-container>
    </ng-container>
  `,
  styleUrls: ['styles.scss']
})

export class PngQueryInputComponent extends BaseComponent implements OnInit, OnChanges {

  DATA_TYPE = DataType;

  DATA_ATTR = DataAttr;

  @Input() queryKey: string;

  @Input() queryDataType: DataType;

  @Input() queryDataAttr: DataAttr;

  @Input() options = {};

  @Input() autoProps: {};

  @Input() sliderProps: {};

  @Output()
  queryValueChange = new EventEmitter<string>();

  isTime = false;

  autoCompleteProps$: BehaviorSubject<PngAutoCompleteProps> = new BehaviorSubject<PngAutoCompleteProps>(undefined);

  constructor(private pngTable: PngTableComponent) {
    super();
  }

  private _queryValue;

  @Input()
  get queryValue() {
    return this._queryValue;
  }

  set queryValue(queryValue) {
    this._queryValue = queryValue;
    this.queryValueChange.emit(queryValue);
  }

  ngOnInit() {
    super.ngOnInit();
    this.autoProps = <PngAutoCompleteProps> {
      inputStyle: {'min-width': '200px'},
      appendTo: 'body',
      forceSelection: false,
      $ext: {
        apiDataProps: {
          ...this.pngTable.getTableQuery(),
          searchFields: this.queryKey,
          returnFields: this.queryKey,
          aggProps: {name: this.queryKey, field: this.queryKey},
          dataPath: `aggs.${this.queryKey}`
        },
        dataItemProps: {
          showCounter: true,
          dropDownOptionKey: DataItemUtils.ITEM_LABEL_KEY,
          valueKey: DataItemUtils.ITEM_LABEL_KEY
        }
      }
    };
    this.sliderProps = <PngSliderProps> {
      range: true,
      $ext: {
        apiDataProps: {
          ...this.pngTable.getTableQuery(),
          aggProps: [
            {name: 'max', field: this.queryKey, method: AggMethods.MAX},
            {name: 'min', field: this.queryKey, method: AggMethods.MIN}
          ]
        }
      }
    };
    if (this.queryDataType === DataType.DATE && this.queryDataAttr) {
      const attrs = CommonsUtils.getArrayValue(this.queryDataAttr);
      if (attrs.indexOf(DataAttr.DATE) >= 0) {
        this.isTime = false;
      }
      if (attrs.indexOf(DataAttr.TIME) >= 0) {
        this.isTime = true;
      }
    }
  }
}
