import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges} from '@angular/core';
import {ApiUtils, BaseComponent} from '@er/core';
import {ApiDataProps, ChartDataProps, ChartDataSetsProps, ChartDimension, ChartType, DataItemProps, DataSource} from '@er/types';
import {ChartUtils, CommonsUtils, IdUtils} from '@er/utils';
import {Query} from 'elastic-builder';
import {BehaviorSubject} from 'rxjs';

@Component({
  selector: 'png-chart',
  template: `
    <p-panel [toggleable]="true" [showHeader]="!!caption">
      <p-header>
        <i [ngClass]="captionIcon || 'fa fa-bar-chart'"></i>
        <span [innerHTML]="caption|erSafeHtml"></span>
        <ng-container *ngTemplateOutlet="templates[templateNames.header]"></ng-container>
      </p-header>
      <er-http-status class="mr-2" [tag]="$id"></er-http-status>
      <div class="p-3" *ngIf="switchable">
        <p-selectButton erPosition="center" [options]="chartTypes" [(ngModel)]="type" (onChange)="changeType()"></p-selectButton>
      </div>
      <p-chart
        [type]="type"
        [options]="options"
        [data]="data$|async"
        [plugins]="plugins"
        [width]="width "
        [height]="height"
        [responsive]="responsive"
        (onDataSelect)="onDataSelect.emit($event)"
      >
      </p-chart>
    </p-panel>

  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class PngChartComponent extends BaseComponent implements OnInit, OnChanges {

  @Input() $id: string;
  @Input() type = ChartType.BAR;
  @Input() rawData: {}[];
  @Input() options: {};
  @Input() caption: string;
  @Input() captionIcon: string;
  @Input() query?: Query | Query[];
  @Input() plugins: any[] = [];
  @Input() width: string;
  @Input() height: string;
  @Input() responsive = true;
  @Input() switchable = true;
  @Input() dataSource: DataSource | ApiDataProps | ApiDataProps[];
  @Input() dataItemProps: DataItemProps;
  @Input() dimensions: ChartDimension | ChartDimension[] | ((rawData) => ChartDataProps);
  @Input() resolveDataSource: { method: ((ds: DataSource) => DataSource) };
  @Input() resolveDimension: { method: ((dimension: ChartDimension) => ChartDimension) };

  templateNames = {
    header: 'header'
  };

  @Output() onDataSelect: EventEmitter<any> = new EventEmitter();

  data$: BehaviorSubject<{}> = new BehaviorSubject<{}>(undefined);

  chartTypes = [
    {label: '', value: ChartType.BAR, icon: 'fa fa-bar-chart'},
    {label: '', value: ChartType.PIE, icon: 'fa fa-pie-chart'},
    {label: '', value: ChartType.LINE, icon: 'fa fa-line-chart'},
    {label: '', value: ChartType.RADAR, icon: 'fa fa-bar'},
    {label: '', value: ChartType.AREA, icon: 'fa fa-area-chart'},
    {label: '', value: ChartType.DOUGHNUT, icon: 'fa fa-circle'}
  ];

  @Input() get data() {
    return this.data$.value;
  }

  set data(data) {
    this.data$.next(data || {});
  }

  ngOnChanges(changes: SimpleChanges) {
    this.rawData = undefined;
    this.buildChart();
  }

  changeType() {
    this.buildChart();
  }

  reBuildChart() {
    this.rawData = undefined;
    this.buildChart();
  }

  buildChart() {
    if (!this.$id) {
      this.$id = IdUtils.getRandom();
    }
    if (this.rawData) {
      this.resolvedRawData(this.rawData);
    } else if (this.dataSource) {
      let ds = this.dataSource;
      if (!ds.hasOwnProperty('apiDataProps')) {
        const apiDataProps = ds;
        ds = {};
        ds['apiDataProps'] = apiDataProps;
      }

      if (this.resolveDataSource) {
        ds = this.resolveDataSource.method(<DataSource>ds);
      }

      ApiUtils.batchFetch(ds, this.$id)
        .subscribe(data => {
          this.rawData = data;
          this.resolvedRawData(this.rawData);
        });
    }
  }

  resolvedRawData(rawData) {
    if (!CommonsUtils.isEmpty(rawData)) {
      let data: ChartDataProps;
      const datasets: ChartDataSetsProps[] = [];
      let labels: string[] = [];
      if (CommonsUtils.isFunction(this.dimensions)) {
        data = (<Function>this.dimensions)(rawData);
      } else {
        if (this.dimensions) {
          const dimensions = CommonsUtils.getArrayValue(this.dimensions);
          dimensions.forEach((dimension, index) => {
            if (this.resolveDimension) {
              dimension = this.resolveDimension.method(dimension);
            }
            const chartData = ChartUtils.buildChartData(rawData, {
              type: this.type,
              dimension: dimension,
              dataItemProps: this.dataItemProps
            }, index);
            datasets.push(...chartData.datasets);
            if (labels.length === 0) {
              labels = chartData.labels;
            }
          });
          data = {labels, datasets};
        } else {
          data = ChartUtils.buildChartData(rawData, {
            type: this.type,
            dataItemProps: this.dataItemProps
          });
        }
      }
      this.data = data;
    } else {
      this.data = {};
    }
  }
}
